import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { Helmet } from 'react-helmet-async'
import toast from 'react-hot-toast'
import { useAuth } from '../contexts/AuthContext'
import { 
  MapPin, 
  Building2,
  X,
  CreditCard,
  Lock
} from 'lucide-react'
import { propertiesApi, api } from '../services/api'
import LoadingSpinner from '../components/LoadingSpinner'

const PropertyDetail = () => {
  const { id } = useParams<{ id: string }>()
  const { isAuthenticated } = useAuth()
  const [property, setProperty] = useState<any>(null)
  const [loading, setLoading] = useState(true)
  const [selectedDates, setSelectedDates] = useState({
    startDate: new Date().toISOString().split('T')[0], // Today
    endDate: '',
  })
  const [showBookingForm, setShowBookingForm] = useState(false)
  const [showPaymentForm, setShowPaymentForm] = useState(false)
  const [bookingLoading, setBookingLoading] = useState(false)
  const [paymentLoading, setPaymentLoading] = useState(false)
  const [bookingData, setBookingData] = useState<any>(null)
  const [paymentIntent, setPaymentIntent] = useState<any>(null)

  useEffect(() => {
    const fetchProperty = async () => {
      if (!id) return
      
      try {
        setLoading(true)
        const response = await propertiesApi.getById(id)
        setProperty(response.data)
      } catch (error) {
        console.error('Failed to fetch property:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchProperty()
  }, [id])

  const calculateEndDate = (startDate: string, days: number = 3) => {
    const date = new Date(startDate)
    date.setDate(date.getDate() + days)
    return date.toISOString().split('T')[0]
  }

  const handleStartDateChange = (date: string) => {
    const endDate = calculateEndDate(date)
    setSelectedDates({
      startDate: date,
      endDate: endDate
    })
  }

  const createPaymentIntent = async (bookingData: any) => {
    try {
      setPaymentLoading(true)
      const response = await api.post('/bookings/create-payment-intent', {
        propertyId: bookingData.propertyId,
        startDate: bookingData.startDate,
        endDate: bookingData.endDate,
        amount: bookingData.amount
      })
      
      setPaymentIntent(response.data.data)
      setShowPaymentForm(true)
    } catch (error: any) {
      toast.error(error.response?.data?.error || 'Failed to create payment intent')
    } finally {
      setPaymentLoading(false)
    }
  }

  const processPayment = async (paymentMethodId: string) => {
    try {
      setPaymentLoading(true)
      await api.post('/bookings/confirm-payment', {
        paymentIntentId: paymentIntent.id,
        paymentMethodId: paymentMethodId,
        bookingData: bookingData
      })
      
      toast.success('Payment successful! Booking confirmed.')
      setShowPaymentForm(false)
      setShowBookingForm(false)
      // Refresh property data to show updated status
      window.location.reload()
    } catch (error: any) {
      toast.error(error.response?.data?.error || 'Payment failed')
    } finally {
      setPaymentLoading(false)
    }
  }

  const handleBooking = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!isAuthenticated) {
      toast.error('Please login to book this property')
      return
    }

    if (!selectedDates.startDate || !selectedDates.endDate) {
      toast.error('Please select booking dates')
      return
    }

    setBookingLoading(true)
    try {
      const data = {
        propertyId: id!,
        startDate: selectedDates.startDate,
        endDate: selectedDates.endDate,
        amount: property?.data?.price || 0,
      }

      setBookingData(data)
      await createPaymentIntent(data)
    } catch (error) {
      console.error('Booking failed:', error)
    } finally {
      setBookingLoading(false)
    }
  }

  if (loading) {
    return <LoadingSpinner />
  }

  if (!property) {
    return (
      <div className="text-center py-8">
        <p className="text-red-600">Property not found</p>
      </div>
    )
  }

  const prop = property.data

  return (
    <>
      <Helmet>
        <title>{prop.title} - Property Platform</title>
      </Helmet>

      <div className="max-w-6xl mx-auto">
        {/* Property Images */}
        <div className="mb-8">
          {prop.mediaUrls && prop.mediaUrls.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="md:col-span-2">
                <img
                  src={prop.mediaUrls[0]}
                  alt={prop.title}
                  className="w-full h-96 object-cover rounded-lg"
                />
              </div>
              {prop.mediaUrls.slice(1, 5).map((url: string, index: number) => (
                <img
                  key={index}
                  src={url}
                  alt={`${prop.title} ${index + 2}`}
                  className="w-full h-48 object-cover rounded-lg"
                />
              ))}
            </div>
          ) : (
            <div className="w-full h-96 bg-gray-200 rounded-lg flex items-center justify-center">
              <Building2 className="h-16 w-16 text-gray-400" />
            </div>
          )}
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Property Details */}
          <div className="lg:col-span-2">
            <div className="card">
              <div className="flex justify-between items-start mb-6">
                <div>
                  <h1 className="text-3xl font-bold text-gray-900 mb-2">{prop.title}</h1>
                  <div className="flex items-center text-gray-600 mb-4">
                    <MapPin className="h-5 w-5 mr-2" />
                    <span>{prop.location}</span>
                  </div>
                </div>
                <div className="text-right">
                  <div className="text-3xl font-bold text-green-600">
                    ${prop.price.toLocaleString()}
                  </div>
                  <div className={`px-3 py-1 rounded-full text-sm font-medium inline-block mt-2 ${
                    prop.status === 'FREE' ? 'bg-green-100 text-green-800' :
                    prop.status === 'BOOKED' ? 'bg-yellow-100 text-yellow-800' :
                    'bg-red-100 text-red-800'
                  }`}>
                    {prop.status}
                  </div>
                </div>
              </div>

              <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
                <div className="text-center">
                  <div className="text-2xl font-bold text-gray-900">{prop.type}</div>
                  <div className="text-sm text-gray-600">Type</div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold text-gray-900">
                    {new Date(prop.createdAt).toLocaleDateString()}
                  </div>
                  <div className="text-sm text-gray-600">Listed</div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold text-gray-900">
                    {prop.owner?.name || 'Anonymous'}
                  </div>
                  <div className="text-sm text-gray-600">Owner</div>
                </div>
                {prop.dealer && (
                  <div className="text-center">
                    <div className="text-2xl font-bold text-gray-900">
                      {prop.dealer.user?.name}
                    </div>
                    <div className="text-sm text-gray-600">Dealer</div>
                  </div>
                )}
              </div>

              <div className="mb-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-3">Description</h3>
                <p className="text-gray-700 leading-relaxed">{prop.description}</p>
              </div>

              {prop.dealer && (
                <div className="bg-blue-50 rounded-lg p-4 mb-6">
                  <h3 className="text-lg font-semibold text-blue-900 mb-2">Dealer Information</h3>
                  <p className="text-blue-800">
                    This property is managed by {prop.dealer.user?.name}. 
                    Contact them for more information about this listing.
                  </p>
                </div>
              )}
            </div>
          </div>

          {/* Booking Section */}
          <div className="lg:col-span-1">
            <div className="card sticky top-8">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Book This Property</h3>
              
              {prop.status === 'FREE' ? (
                <div>
                  <div className="mb-4">
                    <div className="flex items-center justify-between mb-2">
                      <span className="text-gray-600">Price per day</span>
                      <span className="font-semibold">₹{(prop.price / 30).toFixed(2)}</span>
                    </div>
                    <div className="flex items-center justify-between mb-2">
                      <span className="text-gray-600">Minimum booking</span>
                      <span className="font-semibold">3 days</span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-gray-600">Total price (3 days)</span>
                      <span className="font-semibold text-lg text-green-600">
                        ₹{prop.price}
                      </span>
                    </div>
                  </div>

                  {isAuthenticated ? (
                    <button
                      onClick={() => setShowBookingForm(true)}
                      className="w-full btn btn-primary"
                    >
                      Book Now
                    </button>
                  ) : (
                    <div className="text-center">
                      <p className="text-gray-600 mb-4">Please login to book this property</p>
                      <a href="/login" className="btn btn-primary">
                        Login to Book
                      </a>
                    </div>
                  )}
                </div>
              ) : (
                <div className="text-center py-4">
                  <X className="h-12 w-12 text-gray-400 mx-auto mb-2" />
                  <p className="text-gray-600">This property is not available for booking</p>
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Booking Form Modal */}
        {showBookingForm && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-lg p-6 max-w-md w-full mx-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Book Property</h3>
              
              <form onSubmit={handleBooking}>
                <div className="space-y-4">
                  <div>
                    <label className="label">Start Date</label>
                    <input
                      type="date"
                      required
                      value={selectedDates.startDate}
                      onChange={(e) => handleStartDateChange(e.target.value)}
                      className="input"
                      min={new Date().toISOString().split('T')[0]}
                    />
                  </div>
                  
                  <div>
                    <label className="label">End Date</label>
                    <input
                      type="date"
                      required
                      value={selectedDates.endDate}
                      onChange={(e) => setSelectedDates({ ...selectedDates, endDate: e.target.value })}
                      className="input"
                      min={selectedDates.startDate || new Date().toISOString().split('T')[0]}
                    />
                  </div>

                  <div className="bg-gray-50 rounded-lg p-4">
                    <div className="flex justify-between items-center mb-2">
                      <span className="text-gray-600">Total Amount</span>
                      <span className="font-semibold text-lg text-green-600">
                        ₹{prop.price}
                      </span>
                    </div>
                    <p className="text-sm text-gray-500">
                      Payment will be processed securely through Stripe
                    </p>
                  </div>
                </div>

                <div className="flex space-x-3 mt-6">
                  <button
                    type="button"
                    onClick={() => setShowBookingForm(false)}
                    className="flex-1 btn btn-secondary"
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    disabled={bookingLoading}
                    className="flex-1 btn btn-primary disabled:opacity-50"
                  >
                    {bookingLoading ? 'Processing...' : 'Confirm Booking'}
                  </button>
                </div>
              </form>
            </div>
          </div>
        )}

        {/* Payment Form Modal */}
        {showPaymentForm && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-lg p-6 max-w-md w-full mx-4">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold text-gray-900">Secure Payment</h3>
                <button
                  onClick={() => setShowPaymentForm(false)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <X className="h-5 w-5" />
                </button>
              </div>
              
              <div className="space-y-4">
                <div className="bg-blue-50 rounded-lg p-4">
                  <div className="flex items-center mb-2">
                    <Lock className="h-4 w-4 text-blue-600 mr-2" />
                    <span className="text-sm font-medium text-blue-900">Secure Payment</span>
                  </div>
                  <p className="text-sm text-blue-700">
                    Your payment information is encrypted and secure
                  </p>
                </div>

                <div className="bg-gray-50 rounded-lg p-4">
                  <div className="flex justify-between items-center mb-2">
                    <span className="text-gray-600">Booking Amount</span>
                    <span className="font-semibold text-lg text-green-600">
                      ₹{bookingData?.amount || 0}
                    </span>
                  </div>
                  <div className="text-sm text-gray-500">
                    {bookingData?.startDate} to {bookingData?.endDate}
                  </div>
                </div>

                {/* Payment Method Selection */}
                <div>
                  <label className="label">Payment Method</label>
                  <div className="space-y-2">
                    <label className="flex items-center p-3 border rounded-lg cursor-pointer hover:bg-gray-50">
                      <input
                        type="radio"
                        name="paymentMethod"
                        value="card"
                        defaultChecked
                        className="mr-3"
                      />
                      <CreditCard className="h-5 w-5 text-gray-400 mr-2" />
                      <span>Credit/Debit Card</span>
                    </label>
                    <label className="flex items-center p-3 border rounded-lg cursor-pointer hover:bg-gray-50">
                      <input
                        type="radio"
                        name="paymentMethod"
                        value="upi"
                        className="mr-3"
                      />
                      <span>UPI</span>
                    </label>
                  </div>
                </div>

                {/* Mock Payment Form */}
                <div className="space-y-3">
                  <div>
                    <label className="label">Card Number</label>
                    <input
                      type="text"
                      placeholder="1234 5678 9012 3456"
                      className="input"
                      maxLength={19}
                    />
                  </div>
                  <div className="grid grid-cols-2 gap-3">
                    <div>
                      <label className="label">Expiry Date</label>
                      <input
                        type="text"
                        placeholder="MM/YY"
                        className="input"
                        maxLength={5}
                      />
                    </div>
                    <div>
                      <label className="label">CVV</label>
                      <input
                        type="text"
                        placeholder="123"
                        className="input"
                        maxLength={3}
                      />
                    </div>
                  </div>
                </div>
              </div>

              <div className="flex space-x-3 mt-6">
                <button
                  type="button"
                  onClick={() => setShowPaymentForm(false)}
                  className="flex-1 btn btn-secondary"
                >
                  Cancel
                </button>
                <button
                  onClick={() => processPayment('mock_payment_method_id')}
                  disabled={paymentLoading}
                  className="flex-1 btn btn-primary disabled:opacity-50"
                >
                  {paymentLoading ? 'Processing Payment...' : 'Pay ₹' + (bookingData?.amount || 0)}
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </>
  )
}

export default PropertyDetail 