import { useState, useEffect } from 'react'
import { Helmet } from 'react-helmet-async'
import { Search, Filter, MapPin, DollarSign, Building2 } from 'lucide-react'
import { propertiesApi } from '../services/api'
import PropertyCard from '../components/property/PropertyCard'
import PropertyFilters from '../components/property/PropertyFilters'
import LoadingSpinner from '../components/LoadingSpinner'
import { useAuth } from '../contexts/AuthContext'

const Home = () => {
  const { user } = useAuth()
  const isAdmin = user?.role === 'ADMIN'
  
  const [filters, setFilters] = useState({
    type: '',
    location: '',
    minPrice: '',
    maxPrice: '',
    status: isAdmin ? '' : 'FREE', // Default to FREE for non-admin users
  })
  const [showFilters, setShowFilters] = useState(false)

  const [data, setData] = useState<any>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState<string>('')
  const [stats, setStats] = useState({
    totalCities: 0,
    totalCustomers: 0
  })

  useEffect(() => {
    const fetchProperties = async () => {
      try {
        setIsLoading(true)
        console.log('Fetching properties with filters:', filters)
        const response = await propertiesApi.getAll(filters)
        console.log('API Response:', response.data)
        setData(response.data.data) // Fix: access response.data.data instead of response.data
        setError('')
      } catch (err: any) {
        console.error('Error fetching properties:', err)
        setError(err.message)
      } finally {
        setIsLoading(false)
      }
    }

    const fetchStats = async () => {
      try {
        // Fetch actual stats from backend
        const citiesResponse = await propertiesApi.getLocations()
        const cities = citiesResponse.data.data || []
        
        // For now, use a reasonable estimate for customers
        // In a real app, this would come from a public stats endpoint
        const customers = Math.max(1000, cities.length * 20) // Estimate based on cities
        
        setStats({
          totalCities: cities.length,
          totalCustomers: customers
        })
      } catch (error) {
        console.error('Failed to fetch stats:', error)
        // Fallback to default values
        setStats({
          totalCities: 50,
          totalCustomers: 1000
        })
      }
    }

    fetchProperties()
    fetchStats()
  }, [filters])

  const properties = data?.properties || []
  const pagination = data?.pagination



  return (
    <>
      <Helmet>
        <title>Property Platform - Find Your Perfect Property</title>
        <meta name="description" content="Discover amazing properties for sale and rent. Browse through our extensive collection of houses, apartments, and commercial properties." />
      </Helmet>

      <div className="max-w-7xl mx-auto">
        {/* Debug Info - Removed */}
        
        {/* Hero Section */}
        <div className="bg-gradient-to-r from-primary-600 to-primary-700 rounded-lg p-8 mb-8 text-white">
          <div className="text-center">
            <h1 className="text-4xl font-bold mb-4">
              Find Your Perfect Property
            </h1>
            <p className="text-xl mb-8 text-primary-100">
              Discover amazing properties for sale and rent across the country
            </p>
            
            {/* Search Bar */}
            <div className="max-w-2xl mx-auto">
              <div className="flex bg-white rounded-lg shadow-lg overflow-hidden">
                <div className="flex-1 flex items-center px-4">
                  <Search className="h-5 w-5 text-gray-400 mr-3" />
                  <input
                    type="text"
                    placeholder="Search by location"
                    className="flex-1 py-3 text-gray-900 placeholder-gray-500 focus:outline-none"
                    value={filters.location}
                    onChange={(e) => setFilters(prev => ({ ...prev, location: e.target.value }))}
                  />
                </div>
                <button
                  onClick={() => setShowFilters(!showFilters)}
                  className="px-6 py-3 bg-primary-600 hover:bg-primary-700 text-white font-medium transition-colors flex items-center gap-2"
                >
                  <Filter className="h-5 w-5" />
                  <span className="hidden sm:inline">Filters</span>
                </button>
              </div>
            </div>
            
            {/* Active Filters Display */}
            {Object.values(filters).some(value => value !== '') && (
              <div className="mt-4 flex flex-wrap gap-2 justify-center">
                {filters.type && (
                  <span className="bg-white bg-opacity-20 px-3 py-1 rounded-full text-sm">
                    Type: {filters.type}
                  </span>
                )}
                {filters.location && (
                  <span className="bg-white bg-opacity-20 px-3 py-1 rounded-full text-sm">
                    Location: {filters.location}
                  </span>
                )}
                {filters.minPrice && (
                  <span className="bg-white bg-opacity-20 px-3 py-1 rounded-full text-sm">
                    Min: ₹{filters.minPrice}
                  </span>
                )}
                {filters.maxPrice && (
                  <span className="bg-white bg-opacity-20 px-3 py-1 rounded-full text-sm">
                    Max: ₹{filters.maxPrice}
                  </span>
                )}
                {filters.status && (
                  <span className="bg-white bg-opacity-20 px-3 py-1 rounded-full text-sm">
                    Status: {filters.status}
                  </span>
                )}
                <button
                  onClick={() => setFilters({
                    type: '',
                    location: '',
                    minPrice: '',
                    maxPrice: '',
                    status: '',
                  })}
                  className="bg-white bg-opacity-20 px-3 py-1 rounded-full text-sm hover:bg-opacity-30 transition-colors"
                >
                  Clear All
                </button>
              </div>
            )}
          </div>
        </div>

        {/* Filters */}
        {showFilters && (
          <div className="mb-8">
            <PropertyFilters
              filters={filters}
              onFiltersChange={setFilters}
            />
          </div>
        )}

        {/* Stats */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <div className="card text-center">
            <Building2 className="h-8 w-8 text-primary-600 mx-auto mb-2" />
            <h3 className="text-2xl font-bold text-gray-900">
              {pagination?.total || 0}
            </h3>
            <p className="text-gray-600">Properties Available</p>
          </div>
          <div className="card text-center">
            <MapPin className="h-8 w-8 text-primary-600 mx-auto mb-2" />
            <h3 className="text-2xl font-bold text-gray-900">{stats.totalCities}+</h3>
            <p className="text-gray-600">Cities Covered</p>
          </div>
          <div className="card text-center">
            <DollarSign className="h-8 w-8 text-primary-600 mx-auto mb-2" />
            <h3 className="text-2xl font-bold text-gray-900">{stats.totalCustomers}+</h3>
            <p className="text-gray-600">Happy Customers</p>
          </div>
        </div>

        {/* Properties Grid */}
        <div className="mb-8">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-bold text-gray-900">
              Featured Properties
            </h2>
            <div className="text-sm text-gray-600">
              Showing {properties.length} of {pagination?.total || 0} properties
            </div>
          </div>

          {isLoading ? (
            <LoadingSpinner />
          ) : error ? (
            <div className="text-center py-8">
              <p className="text-red-600">Failed to load properties. Please try again.</p>
            </div>
          ) : properties.length === 0 ? (
            <div className="text-center py-8">
              <Building2 className="h-12 w-12 text-gray-400 mx-auto mb-4" />
              <p className="text-gray-600">No properties found matching your criteria.</p>
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {properties.map((property: any) => (
                <PropertyCard key={property.id} property={property} />
              ))}
            </div>
          )}
        </div>

        {/* Pagination */}
        {pagination && pagination.pages > 1 && (
          <div className="flex justify-center">
            <nav className="flex space-x-2">
              {Array.from({ length: pagination.pages }, (_, i) => i + 1).map((page) => (
                <button
                  key={page}
                  className={`px-3 py-2 rounded-md text-sm font-medium ${
                    page === pagination.page
                      ? 'bg-primary-600 text-white'
                      : 'bg-white text-gray-700 hover:bg-gray-50 border border-gray-300'
                  }`}
                >
                  {page}
                </button>
              ))}
            </nav>
          </div>
        )}
      </div>
    </>
  )
}

export default Home 