import { useState, useEffect } from 'react'
import { Helmet } from 'react-helmet-async'
import { 
  Users, 
  Building2, 
  DollarSign, 
  TrendingUp,
  CheckCircle,
  Activity
} from 'lucide-react'
import { adminApi, commissionApi } from '../services/api'
import LoadingSpinner from '../components/LoadingSpinner'

const Admin = () => {
  const [dashboardData, setDashboardData] = useState<any>(null)
  const [propertyAnalytics, setPropertyAnalytics] = useState<any>(null)
  const [bookingAnalytics, setBookingAnalytics] = useState<any>(null)
  const [dealerAnalytics, setDealerAnalytics] = useState<any>(null)
  const [recentActivity, setRecentActivity] = useState<any>(null)
  const [commissionConfig, setCommissionConfig] = useState<any>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true)
        
        // Fetch all data in parallel
        const [
          dashboardResponse,
          propertyResponse,
          bookingResponse,
          dealerResponse,
          activityResponse,
          configResponse
        ] = await Promise.all([
          adminApi.getDashboard(),
          adminApi.getPropertyAnalytics(),
          adminApi.getBookingAnalytics(),
          adminApi.getDealerAnalytics(),
          adminApi.getRecentActivity(),
          commissionApi.getConfig()
        ])

        setDashboardData(dashboardResponse.data)
        setPropertyAnalytics(propertyResponse.data)
        setBookingAnalytics(bookingResponse.data)
        setDealerAnalytics(dealerResponse.data)
        setRecentActivity(activityResponse.data)
        setCommissionConfig(configResponse.data)
      } catch (error) {
        console.error('Failed to fetch admin data:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [])

  if (loading) {
    return <LoadingSpinner />
  }

  const stats = dashboardData || {}
  const properties = propertyAnalytics || {}
  const bookings = bookingAnalytics || {}
  const dealers = dealerAnalytics || {}
  const activities = recentActivity || []
  const config = commissionConfig || []

  return (
    <>
      <Helmet>
        <title>Admin Dashboard - Property Platform</title>
      </Helmet>

      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">Admin Dashboard</h1>
          <p className="text-gray-600 mt-2">
            Manage your property platform and monitor performance
          </p>
        </div>

        {/* Stats Overview */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <div className="card">
            <div className="flex items-center">
              <div className="p-3 rounded-lg bg-blue-100">
                <Users className="h-6 w-6 text-blue-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Total Users</p>
                <p className="text-2xl font-bold text-gray-900">{stats.totalUsers || 0}</p>
              </div>
            </div>
          </div>

          <div className="card">
            <div className="flex items-center">
              <div className="p-3 rounded-lg bg-green-100">
                <Building2 className="h-6 w-6 text-green-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Total Properties</p>
                <p className="text-2xl font-bold text-gray-900">{stats.totalProperties || 0}</p>
              </div>
            </div>
          </div>

          <div className="card">
            <div className="flex items-center">
              <div className="p-3 rounded-lg bg-purple-100">
                <DollarSign className="h-6 w-6 text-purple-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Total Revenue</p>
                <p className="text-2xl font-bold text-gray-900">
                  ${(stats.totalRevenue || 0).toLocaleString()}
                </p>
              </div>
            </div>
          </div>

          <div className="card">
            <div className="flex items-center">
              <div className="p-3 rounded-lg bg-orange-100">
                <TrendingUp className="h-6 w-6 text-orange-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Total Bookings</p>
                <p className="text-2xl font-bold text-gray-900">{stats.totalBookings || 0}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Analytics Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8">
          {/* Property Analytics */}
          <div className="card">
            <h3 className="text-lg font-semibold text-gray-900 mb-6">Property Analytics</h3>
            
            <div className="space-y-4">
              <div>
                <h4 className="font-medium text-gray-700 mb-2">Properties by Status</h4>
                <div className="space-y-2">
                  {properties.byStatus?.map((item: any) => (
                    <div key={item.status} className="flex justify-between items-center">
                      <span className="text-sm text-gray-600">{item.status}</span>
                      <span className="font-medium">{item._count.id}</span>
                    </div>
                  ))}
                </div>
              </div>

              <div>
                <h4 className="font-medium text-gray-700 mb-2">Properties by Type</h4>
                <div className="space-y-2">
                  {properties.byType?.slice(0, 5).map((item: any) => (
                    <div key={item.type} className="flex justify-between items-center">
                      <span className="text-sm text-gray-600">{item.type}</span>
                      <span className="font-medium">{item._count.id}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Booking Analytics */}
          <div className="card">
            <h3 className="text-lg font-semibold text-gray-900 mb-6">Booking Analytics</h3>
            
            <div className="space-y-4">
              <div>
                <h4 className="font-medium text-gray-700 mb-2">Bookings by Status</h4>
                <div className="space-y-2">
                  {bookings.byStatus?.map((item: any) => (
                    <div key={item.status} className="flex justify-between items-center">
                      <span className="text-sm text-gray-600">{item.status}</span>
                      <span className="font-medium">{item._count.id}</span>
                    </div>
                  ))}
                </div>
              </div>

              <div>
                <h4 className="font-medium text-gray-700 mb-2">Monthly Revenue</h4>
                <div className="space-y-2">
                  {bookings.revenueByMonth?.slice(0, 5).map((item: any, index: number) => (
                    <div key={index} className="flex justify-between items-center">
                      <span className="text-sm text-gray-600">
                        {new Date(item.createdAt).toLocaleDateString('en-US', { month: 'short' })}
                      </span>
                      <span className="font-medium">${(item._sum.amount || 0).toFixed(2)}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Dealer Analytics */}
        <div className="card mb-8">
          <h3 className="text-lg font-semibold text-gray-900 mb-6">Dealer Analytics</h3>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="text-center">
              <div className="text-3xl font-bold text-gray-900">{dealers.totalDealers || 0}</div>
              <div className="text-sm text-gray-600">Total Dealers</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-gray-900">
                ${(dealers.totalCommissions || 0).toFixed(2)}
              </div>
              <div className="text-sm text-gray-600">Total Commissions Paid</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-gray-900">
                {dealers.totalCommissionTransactions || 0}
              </div>
              <div className="text-sm text-gray-600">Commission Transactions</div>
            </div>
          </div>
        </div>

        {/* Commission Configuration */}
        <div className="card mb-8">
          <h3 className="text-lg font-semibold text-gray-900 mb-6">Commission Structure</h3>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {config.map((level: any) => (
              <div key={level.level} className="text-center p-4 border border-gray-200 rounded-lg">
                <div className="text-2xl font-bold text-gray-900">Level {level.level}</div>
                <div className="text-3xl font-bold text-green-600">{level.percentage}%</div>
                <div className="text-sm text-gray-600">Commission Rate</div>
              </div>
            ))}
          </div>
        </div>

        {/* Recent Activity */}
        <div className="card">
          <h3 className="text-lg font-semibold text-gray-900 mb-6">Recent Activity</h3>
          
          <div className="space-y-4">
            {activities.slice(0, 10).map((activity: any, index: number) => (
              <div key={index} className="flex items-center justify-between p-4 border border-gray-200 rounded-lg">
                <div className="flex items-center">
                  <div className="p-2 rounded-lg bg-gray-100 mr-4">
                    {activity.type === 'property' && <Building2 className="h-5 w-5 text-blue-600" />}
                    {activity.type === 'booking' && <Activity className="h-5 w-5 text-green-600" />}
                    {activity.type === 'payment' && <DollarSign className="h-5 w-5 text-purple-600" />}
                    {activity.type === 'dealer' && <Users className="h-5 w-5 text-orange-600" />}
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">
                      {activity.type === 'property' && `New property listed: ${activity.data.title}`}
                      {activity.type === 'booking' && `New booking for: ${activity.data.property.title}`}
                      {activity.type === 'payment' && `Payment received: $${activity.data.amount}`}
                      {activity.type === 'dealer' && `New dealer registered: ${activity.data.user.name}`}
                    </p>
                    <p className="text-sm text-gray-600">
                      {new Date(activity.data.createdAt).toLocaleString()}
                    </p>
                  </div>
                </div>
                <div className="flex items-center">
                  {activity.type === 'payment' && (
                    <CheckCircle className="h-5 w-5 text-green-600 mr-2" />
                  )}
                  <span className="text-xs text-gray-500 capitalize">{activity.type}</span>
                </div>
              </div>
            ))}
            
            {activities.length === 0 && (
              <div className="text-center py-8">
                <Activity className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <p className="text-gray-600">No recent activity</p>
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  )
}

export default Admin 