import { useState, useEffect } from 'react'
import { Helmet } from 'react-helmet-async'
import { useAuth } from '../contexts/AuthContext'
import { 
  Building2, 
  Calendar, 
  DollarSign, 
  Users, 
  TrendingUp,
  Plus,
  Eye
} from 'lucide-react'
import { Link } from 'react-router-dom'
import { bookingsApi, dealersApi, propertiesApi } from '../services/api'
import LoadingSpinner from '../components/LoadingSpinner'

const Dashboard = () => {
  const { user } = useAuth()

  const [bookings, setBookings] = useState<any>(null)
  const [commissions, setCommissions] = useState<any>(null)
  const [properties, setProperties] = useState<any>(null)
  const [bookingsLoading, setBookingsLoading] = useState(true)
  const [commissionsLoading, setCommissionsLoading] = useState(true)
  const [propertiesLoading, setPropertiesLoading] = useState(true)

  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const response = await bookingsApi.getMyBookings()
        setBookings(response.data)
      } catch (error) {
        console.error('Failed to fetch bookings:', error)
      } finally {
        setBookingsLoading(false)
      }
    }

    const fetchCommissions = async () => {
      if (user?.role === 'DEALER') {
        try {
          const response = await dealersApi.getCommissions()
          setCommissions(response.data)
        } catch (error) {
          console.error('Failed to fetch commissions:', error)
        } finally {
          setCommissionsLoading(false)
        }
      } else {
        setCommissionsLoading(false)
      }
    }

    const fetchProperties = async () => {
      if (user?.id) {
        try {
          const response = await propertiesApi.getAll({ ownerId: user.id })
          setProperties(response.data.data) // Fix: access response.data.data
        } catch (error) {
          console.error('Failed to fetch properties:', error)
        } finally {
          setPropertiesLoading(false)
        }
      } else {
        setPropertiesLoading(false)
      }
    }

    fetchBookings()
    fetchCommissions()
    fetchProperties()
  }, [user])

  const stats = [
    {
      name: 'My Properties',
      value: properties?.data?.properties?.length || 0,
      icon: Building2,
      color: 'text-blue-600',
      bgColor: 'bg-blue-100',
    },
    {
      name: 'My Bookings',
      value: bookings?.data?.length || 0,
      icon: Calendar,
      color: 'text-green-600',
      bgColor: 'bg-green-100',
    },
    ...(user?.role === 'DEALER' ? [{
      name: 'Total Commissions',
      value: `$${(commissions?.data?.reduce((sum: number, c: any) => sum + c.amount, 0) || 0).toFixed(2)}`,
      icon: DollarSign,
      color: 'text-purple-600',
      bgColor: 'bg-purple-100',
    }] : []),
    {
      name: 'Network Size',
      value: '0', // This would be calculated from dealer hierarchy
      icon: Users,
      color: 'text-orange-600',
      bgColor: 'bg-orange-100',
    },
  ]

  if (bookingsLoading || commissionsLoading || propertiesLoading) {
    return <LoadingSpinner />
  }

  return (
    <>
      <Helmet>
        <title>Dashboard - Property Platform</title>
      </Helmet>

      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">
            Welcome back, {user?.name || user?.email}!
          </h1>
          <p className="text-gray-600 mt-2">
            Here's what's happening with your properties and bookings.
          </p>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          {stats.map((stat) => {
            const Icon = stat.icon
            return (
              <div key={stat.name} className="card">
                <div className="flex items-center">
                  <div className={`p-3 rounded-lg ${stat.bgColor}`}>
                    <Icon className={`h-6 w-6 ${stat.color}`} />
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-600">{stat.name}</p>
                    <p className="text-2xl font-bold text-gray-900">{stat.value}</p>
                  </div>
                </div>
              </div>
            )
          })}
        </div>

        {/* Quick Actions */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
          <div className="card">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h3>
            <div className="space-y-3">
              <Link
                to="/sell"
                className="flex items-center p-3 rounded-lg border border-gray-200 hover:bg-gray-50 transition-colors"
              >
                <Plus className="h-5 w-5 text-primary-600 mr-3" />
                <span className="font-medium">List a New Property</span>
              </Link>
              <Link
                to="/"
                className="flex items-center p-3 rounded-lg border border-gray-200 hover:bg-gray-50 transition-colors"
              >
                <Eye className="h-5 w-5 text-primary-600 mr-3" />
                <span className="font-medium">Browse Properties</span>
              </Link>
            </div>
          </div>

          <div className="card">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Recent Activity</h3>
            <div className="space-y-3">
              {(bookings?.data || []).slice(0, 3).map((booking: any) => (
                <div key={booking.id} className="flex items-center justify-between p-3 rounded-lg bg-gray-50">
                  <div>
                    <p className="font-medium text-sm">{booking.property.title}</p>
                    <p className="text-xs text-gray-600">
                      {new Date(booking.createdAt).toLocaleDateString()}
                    </p>
                  </div>
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                    booking.status === 'CONFIRMED' ? 'bg-green-100 text-green-800' :
                    booking.status === 'PENDING' ? 'bg-yellow-100 text-yellow-800' :
                    'bg-gray-100 text-gray-800'
                  }`}>
                    {booking.status}
                  </span>
                </div>
              ))}
              {(!bookings?.data || bookings.data.length === 0) && (
                <p className="text-gray-500 text-sm">No recent activity</p>
              )}
            </div>
          </div>
        </div>

        {/* My Properties */}
        <div className="card mb-8">
          <div className="flex justify-between items-center mb-6">
            <h3 className="text-lg font-semibold text-gray-900">My Properties</h3>
            <Link to="/sell" className="btn btn-primary">
              <Plus className="h-4 w-4 mr-2" />
              Add Property
            </Link>
          </div>
          
          {properties?.data?.properties && properties.data.properties.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {(properties?.data?.properties || []).slice(0, 6).map((property: any) => (
                <div key={property.id} className="border border-gray-200 rounded-lg p-4">
                  <img
                    src={property.mediaUrls?.[0] || '/placeholder-property.jpg'}
                    alt={property.title}
                    className="w-full h-32 object-cover rounded-lg mb-3"
                  />
                  <h4 className="font-medium text-gray-900 mb-1">{property.title}</h4>
                  <p className="text-sm text-gray-600 mb-2">{property.location}</p>
                  <div className="flex justify-between items-center">
                    <span className="text-lg font-bold text-gray-900">
                      ${property.price.toLocaleString()}
                    </span>
                    <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                      property.status === 'FREE' ? 'bg-green-100 text-green-800' :
                      property.status === 'BOOKED' ? 'bg-yellow-100 text-yellow-800' :
                      'bg-red-100 text-red-800'
                    }`}>
                      {property.status}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="text-center py-8">
              <Building2 className="h-12 w-12 text-gray-400 mx-auto mb-4" />
              <p className="text-gray-600">You haven't listed any properties yet.</p>
              <Link to="/sell" className="btn btn-primary mt-4">
                List Your First Property
              </Link>
            </div>
          )}
        </div>

        {/* Commissions (Dealer Only) */}
        {user?.role === 'DEALER' && (
          <div className="card">
            <h3 className="text-lg font-semibold text-gray-900 mb-6">Recent Commissions</h3>
            
            {commissions?.data?.length > 0 ? (
              <div className="space-y-4">
                {(commissions?.data || []).slice(0, 5).map((commission: any) => (
                  <div key={commission.id} className="flex items-center justify-between p-4 border border-gray-200 rounded-lg">
                    <div>
                      <p className="font-medium">{commission.property.title}</p>
                      <p className="text-sm text-gray-600">Level {commission.level} commission</p>
                    </div>
                    <div className="text-right">
                      <p className="font-bold text-green-600">${commission.amount.toFixed(2)}</p>
                      <p className="text-xs text-gray-500">
                        {new Date(commission.createdAt).toLocaleDateString()}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center py-8">
                <TrendingUp className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <p className="text-gray-600">No commissions earned yet.</p>
                <p className="text-sm text-gray-500 mt-2">
                  Start building your network to earn commissions!
                </p>
              </div>
            )}
          </div>
        )}
      </div>
    </>
  )
}

export default Dashboard 